.table-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height:60vh;
}

table {
  background-color: #282c34;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}
.RatingText{
  color: whitesmoke;
  text-align: left;
}

.badRating{
  background-color: red;
}
.ehhRating{
  background-color: orange;
}
.goodRating{
  background-color: green;
}


th, td {
  background-color: #f2f2f2;
  text-align: left;
  border-radius: 10px;
  padding: 5px;
}




.AddTable {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 30vh;
}

.movie-form {
  background-color: #282c34;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 80%;
  max-width: 400px;
}

.form-input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  font-size: 16px;
}

.submit-button, .reset_button {
  background-color: lightgreen;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}
.reset_button{
  background-color: orange;
}

